.project {
    width: 100%;
    height: calc(100vh - 100px);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  
  .project h1 {
    margin-top: 8rem;

    padding: 3rem 0;
    color: #3e497a;
    font-size: 35px;
  }
  .project img {
    width: 700px;
    border-radius: 10px;
  }
  
  .project p {
    margin-top: 2rem;
    font-size: 40px;
    color: #3e497a;
  }
  
  .project a {
    margin-top: 2rem;
    text-decoration: none;
    transition: all .3s ease-in-out;
    border-radius: 50%;
    transition: all .4s ease-in-out;
  }

  .project a:hover {
    transform: translateY(-5px);
  }